
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'

import {httpUrl} from '../../http_url';
import * as qiniu from 'qiniu-js'

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: ''
},
showMenus: false,
height:(document.documentElement.clientHeight - 220)+ 'px',
option:{
penColor:"rgb(0, 0, 0)",
backgroundColor:"rgb(255,255,255)"
},
validate_data:'9526',
arrNum:[],
num1:'',
num2:'',
num3:'',
num4:'',
transfinite:'',                     //
realNameStatus:'',
bankStatus:'',
infoStatus:'',
videoBtnShow:false,
qiniu_token:''
}
},
created(){
this.getStatus();
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton
},
methods:{

getStatus(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.creditInformation
}).then((res)=>{
let data = res.data.retData;
if(res.data.errCode==200){
this.realNameStatus = data.realName.status;
this.bankStatus = data.bank.status;
this.infoStatus = data.relevantData.status;
if(this.realNameStatus == 0){
this.$vux.confirm.show({
title:'',
content:"",
confirmText:'',
onCancel () {
_this.$router.push('/credit_info_list');
},
onConfirm () {
_this.$router.push('/credit_info_list');
}
})
}else if(this.bankStatus==0){
this.$vux.confirm.show({
title:'',
content:"",
confirmText:'',
onCancel () {
_this.$router.push('/credit_info_list');
},
onConfirm () {
_this.$router.push('/credit_info_list');
}
})
}else if(this.infoStatus==0){
this.$vux.confirm.show({
title:'',
content:"",
confirmText:'',
onCancel () {
_this.$router.push('/credit_info_list');
},
onConfirm () {
_this.$router.push('/credit_info_list');
}
})
}else {
this.getData();
this.videoBtnShow = true;
}
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '',
content: '',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '',
type:'warn'
})
});
},

getData(){
const _this = this;
let orderid = this.$route.query.orderid;
let postData = this.$qs.stringify({
orderid:orderid
})

this.$axios({
method: 'post',
url:httpUrl.getLipCode,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.validate_data = res.data.retData.code;
this.arrNum = this.validate_data.split('');
this.num1 = this.arrNum[0];
this.num2 = this.arrNum[1];
this.num3 = this.arrNum[2];
this.num4 = this.arrNum[3];
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '',
content: '',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '',
type:'warn'
})
});
},

qiniuUpload(event){
var _this = this;
var file = event.target.files[0];

let orderid = this.$route.query.orderid;
let postData = this.$qs.stringify({
orderid:orderid
});
this.$vux.loading.show({
text: '...'
})
this.$axios({
method: 'post',
url:httpUrl.getVideoToken,               //token
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.qiniu_token = res.data.retData.uptoken;
const key_name = res.data.retData.name;

const formdata = new FormData()
formdata.append('file', file)
formdata.append('key', key_name)
formdata.append('token', this.qiniu_token)
this.$axios({
method: 'post',
url:'http://upload-z2.qiniup.com/',         // 
headers: {
'Content-Type': 'multipart/form-data'
},
withCredentials: false,
data:formdata
}).then((res)=>{
this.$vux.loading.hide();
console.log(res.data)
this.lipCodeCheck();
})
}else if(res.data.errCode==201){
this.$vux.toast.show({
text: res.data.retMsg,
type: 'warn',
onHide () {

}
})
}

})
},

lipCodeCheck(){
const _this = this;
const step_type = this.$route.query.step_type;
let orderid = this.$route.query.orderid;

this.$vux.loading.show({
text: '...'
})
let postData = this.$qs.stringify({
orderid:orderid
})
this.$axios({
method: 'post',
url:httpUrl.lipCodeCheck,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data);
_this.transfinite = res.data.retData.transfinite;
this.$vux.loading.hide();
if(res.data.errCode==200){
this.$vux.toast.show({
text: '',
type:'success',
onHide(){
_this.$router.push({
path:'/live_face_suc',
query: {
orderid:orderid,
step_type:step_type
}
});
}
})
}else if(res.data.errCode==201){
if(this.transfinite == 1){     //6
this.$vux.toast.show({
text: res.data.retMsg+'0755-22211333',
type: 'warn',
onHide () {
_this.$router.push('/home');
}
})
}else {
this.$router.push({
path:'/live_face_fail',
query: {
orderid:orderid,
step_type:step_type,
retMsg:res.data.retMsg,
transfinite:this.transfinite
}
});
}
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '',
content: '',
onHide () {
_this.$router.push('/quick_login');
}
})
}
})
},


updatedata(){
let orderid = this.$route.query.orderid;
const _this = this;
let postData = this.$qs.stringify({
orderid:orderid,
is_sign:1
})
this.$axios({
method: 'post',
url:httpUrl.updatedata,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data);
this.$vux.loading.hide();
if(res.data.errCode==200){

}else if(res.data.errCode==201){
this.$vux.toast.show({
text: res.data.retMsg,
type:'warn',
onHide(){

}
});
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '',
content: '',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '',
type:'warn'
})
});
},

LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '',
type:'warn'
})
});
},

goHome(){
this.$router.push('/home');
}
},

}



